.body header nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

header {
    background: linear-gradient(180deg, rgb(23, 130, 235), rgb(27, 185, 254));
    padding-top: 3rem;
    z-index: -99;
}

html:not([data-scroll='0']) nav {
    background: white;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
}

html:not([data-scroll='0']) nav>.auth p a {
    color: black;
}

html:not([data-scroll='0']) nav>.auth p:nth-child(2) a {
    background: rgb(23, 130, 235);
    color: white;
}

html:not([data-scroll='0']) nav>.logo svg {
    fill: rgb(23, 130, 235);
}

header nav {
    padding: 1rem 3rem;
    background: rgb(23, 130, 235);
    transition: background .3s;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 99;
}

header nav .logo {
    width: 100px;
}

header nav svg {
    fill: white;
}

header .auth {
    display: flex;
    column-gap: 20px;
    font-size: 13px;
}

header .auth p:nth-child(2) a {
    font-weight: 600;
    background: white;
    border-radius: 8px;
    padding: 8px 20px;
    color: rgb(23, 130, 235);
}

header .auth p a {
    text-decoration: none;
    color: white;
}

header .logo img {
    width: 100%;
}

.hero {
    position: relative;
    max-width: 1400px;
    padding: 4rem 1rem;
    margin: 0 auto;
    column-gap: 20px;
}

.img-form {
    display: flex;
    padding: 2rem 0;
}

.hero .back img {
    width: 100%;
    height: 100%;
}

.hero h1 {
    font-size: 45px;
    color: white;
    text-align: center;
    padding: 20px 0;
    padding-bottom: 0;
}

.hero h3 {
    font-size: 20px;
    color: white;
    text-align: center;
    opacity: .5;
}

.hero .back {
    position: absolute;
    height: 50%;
    width: 50%;
    z-index: 1;
    opacity: .3;
    bottom: 30px;
    left: 0;
}

.hero .img-form .img {
    width: 65%;
}

.hero .img-form .img img {
    width: 100%;
}

header .hero .img-form .form {
    background-color: rgba(255, 255, 255, 0.4);
    padding: 1rem;
    border-radius: 8px;
    text-align: center;
    width: 35%;
    border: 1px solid rgba(255, 255, 255, 0.6);
}

header .hero .img-form .form input {
    padding: 10px;
    outline: none;
    border: none;
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid rgba(255, 255, 255, 0.4);
    font-size: 15px;
    border-radius: 6px;
    font-size: 13px;
    background: rgba(0, 0, 0, 0.07);
    color: white;
}

header .hero .img-form .form input::placeholder {
    color: rgba(255, 255, 255, 0.849)
}

header .hero .img-form .form button,
.btn {
    border-radius: 8px;
    font-size: 15px;
    border: none;
    outline: none;
    padding: 15px;
    color: rgb(23, 130, 235);
    background: white;
    width: 100%;
    font-weight: bold;
    cursor: pointer;
    margin: 5px 0;
    text-transform: uppercase;
}

.img-form .form h3 {
    padding: 5px 0;
}

.reasons {
    margin: 0 auto;
    max-width: 1200px;
    padding: 3rem 1rem;
}

.reasons h1 {
    font-size: 30px;
    text-align: center;
    position: relative;
}

.reasons .reasonItem {
    display: flex;
    align-items: center;
}

.reasons .reasonItem:nth-child(3) {
    flex-direction: row-reverse;
}

.reasons .reasonItem .img {
    width: 50%;
}

.reasons .reasonItem .txt {
    width: 50%;
    opacity: 0.8;
}

.reasons .reasonItem .txt h3 {
    font-size: 30px;
    padding: 15px 0;
}

.reasons .reasonItem .img img {
    width: 100%;
}

button:disabled {
    opacity: 0.5;
}

.testimony-outer {
    background: rgb(23, 130, 235);
    color: white;
}

.testimony-outer .testimony {
    max-width: 1200px;
    margin: 0 auto;
    padding: 4.5rem 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.testimony-outer .testimony h3 {
    font-size: 25px;
}

.open-outer {
    background: rgb(245, 247, 250);
}

.open-account {
    max-width: 1200px;
    margin: 0 auto;
    padding: 3rem 1rem;
}

.steps {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 40px;
}

.steps div {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.steps div h3,
.steps div p {
    opacity: .8;
}

.steps div img {
    width: 280px;
    height: 280px;
    object-fit: contain;
    padding: 2rem;
}

.building {
    max-width: 1200px;
    margin: 0 auto;
    padding: 3rem 1rem;
    display: flex;
    align-items: center;
    column-gap: 50px;
}

.building .left {
    width: 40%;
    padding-right: 30px;
}

.building .right {
    width: 60%;
}

.building .left svg {
    fill: rgb(23, 130, 235);
    width: 80px;
}

.building .right .step {
    display: flex;
    align-items: center;
    padding: 15px 10px;
    margin-bottom: 30px;
    box-shadow: 0 0 40px rgba(23, 130, 235, 0.1);
}

.building .right .step h3 {
    opacity: 0.8;
}

.building .right .step .img {
    width: 30%;
    padding: 17px;
}

.building .right .step .txt {
    width: 70%;
}

.building .right a {
    text-decoration: none;
}

.building .right a p {
    opacity: 0.6;
    padding: 10px 0;
    font-size: 14px;
    color: black;
}

.building .right a h3 {
    color: black;
    opacity: 0.9;
}

.building .right .step .img img {
    width: 100%;
}

.advert {
    max-width: 1200px;
    margin: 0 auto;
    background: rgb(245, 247, 250);
    padding: 3rem 2rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 13px;
    margin-bottom: 3rem;
}

.advert img:nth-child(3) {
    width: 50%;
}

.advert img:nth-child(2) {
    width: 30%;
    margin-bottom: 1rem;
}

.advert .benefits {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 3rem 0;
    grid-gap: 25px;
}

.advert .benefits p {
    font-size: 15px;
    opacity: .8;
}

.advert .benefits div {
    background: white;
    padding: 2rem;
}

.advert button {
    border: none;
    outline: none;
    background: rgb(247, 105, 77);
    padding: 10px 80px;
    color: white;
    font-weight: bold;
    border-radius: 8px;
    cursor: pointer;
}

.footer {
    border-top: 2px solid rgb(23, 130, 235);
}

footer {
    max-width: 1200px;
    margin: 0 auto;
    padding: 3rem 1rem;
}

footer .logo {
    width: 150px;
    margin-bottom: 15px;
}

footer svg {
    fill: rgba(23, 129, 235, 0.705);
}

footer p {
    opacity: 0.7;
    font-size: 12px;
}

.drawer {
    color: white;
    font-size: 25px;
    cursor: pointer;
    opacity: .8;
    display: none;
}

html:not([data-scroll='0']) nav>.drawer {
    color: black;
}

html:not([data-scroll='0']) nav>#google_translate_element select {
    background: rgb(23, 130, 235);
    color: white;
}

select {
    outline: none;
    border: none;
    background: white;
    color: rgb(23, 130, 235);
    cursor: pointer;
    padding: 5px 10px;
    font-family: "poppins";
    font-size: 13px;
    border-radius: 3px;
}

#google_translate_element span {
    display: none;
}

select[aria-label] {
    font-size: 10px;
    font-family: "poppins";
}

select option {
    border: none;
    font-family: "poppins";
    font-size: 13px;
}

@media (max-width:900px) {
    .img-form {
        flex-direction: column;
    }
    .hero .img-form .img {
        width: 100%;
        margin-bottom: 1rem;
    }
    header .hero .img-form .form {
        width: 100%;
    }
    .hero .back {
        top: 90px;
        left: 30px;
    }
    .reasons .reasonItem {
        flex-direction: column;
        text-align: center;
    }
    .reasons .reasonItem:nth-child(3) {
        flex-direction: column;
    }
    .reasons .reasonItem .img {
        width: 100%;
    }
    .reasons .reasonItem .txt {
        width: 100%;
    }
    .reasons .reasonItem .txt h3 {
        font-size: 20px;
    }
    .testimony-outer .testimony {
        flex-direction: column;
        text-align: center;
        row-gap: 40px;
    }
    .steps {
        flex-direction: column;
        justify-content: center;
    }
    .building {
        flex-direction: column;
    }
    .building .left {
        width: 100%;
    }
    .building .right {
        width: 100%;
        margin-top: 2rem;
    }
    .advert img:nth-child(3) {
        width: 80%;
    }
    .advert img:nth-child(2) {
        width: 50%;
    }
    .advert .benefits {
        grid-template-columns: auto;
    }
    .auth.lappy {
        display: none;
    }
    .drawer {
        display: block;
    }
    .auth.draw {
        padding: 1rem;
        flex-direction: column;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.6);
        line-height: 40px;
        position: fixed;
        width: 100%;
        z-index: 99;
        top: 79px;
    }
    header nav {
        padding: 1rem 2rem;
    }
}